import React, {PureComponent} from 'react'
import {Modal, Row, Col, Table, Button, Input, message, DatePicker} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxValidateInput, CsxFormSearch} from 'Form'
import moment from 'moment'
import {
  API_NEWPRODUCT_GETPRODUCTKEYVALUES,
  API_STORAGE_SEARCH_GETPRODUCTKEYVALUES
} from 'api'

@connect(
  (state) => ({...state.inWarehouseList, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.foucsName = 'inWarehouseList'
    this.foucsIndex = 1
    this.columns = [
      {
        title: '商品编码',
        width: 150,
        dataIndex: 'productCode',
        key: 'productCode'
      },
      {
        title: '商品条码',
        width: 150,
        dataIndex: 'productBarCode',
        key: 'productBarCode'
      },
      {
        title: '商品名称',
        width: 200,
        dataIndex: 'productName',
        key: 'productName'
      },
      {
        title: '收货',
        width: 150,
        dataIndex: 'expectReceiveQty',
        key: 'expectReceiveQty',
        render: (text, record, index) => (
          <CsxValidateInput
            id={`inWarehouseList${index + 1}`}
            onFocus={(e) => {
              this.elOnFocus(e)
            }}
            defaultValue={
              this.props.modalData.entryType == 'T06' && record.shippedQty
                ? (record.unit && record.unit.toUpperCase()) == 'KG'
                  ? Number(
                      (record.shippedQty - record.receiveQty).toFixed(3)
                    ) || ''
                  : Number(record.shippedQty - record.receiveQty) || ''
                : ''
            }
            style={{width: '100px'}}
            reg={
              (record.unit && record.unit.toUpperCase()) == 'KG'
                ? 'TWOO'
                : 'REGO'
            }
            error={record.receiveQtyInput && record.receiveQtyInput.error}
            handleOnchange={(data) => {
              if (data.value) {
                record.receiveQtyInput = data
              } else {
                record.receiveQtyInput = ''
              }
              record.noDefault = true
              this.forceUpdate()
            }}
          />
        )
      },
      {
        title: '收货单位',
        width: 100,
        dataIndex: 'unit',
        key: 'unit'
      },
      {
        title: '计划数',
        width: 100,
        dataIndex: 'planQty',
        key: 'planQty'
      },
      {
        title: '生产日期',
        width: 150,
        dataIndex: 'produceDate',
        key: 'produceDate',
        render: (text, record, index) => (
          <DatePicker
            placeholder={moment(new Date()).format('YYYY-MM-DD')}
            onChange={(moment, string) => {
              record.produceDate = string
            }}
            format={'YYYY-MM-DD'}
            disabledDate={(current) =>
              current && current.valueOf() > Date.now()
            }
          />
        )
      },
      {
        title: '发货数',
        width: 100,
        dataIndex: 'shippedQty',
        key: 'shippedQty'
      },
      {
        title: '已收数',
        width: 100,
        dataIndex: 'receiveQty',
        key: 'receiveQty'
      },
      {
        title: '收货至',
        width: 100,
        dataIndex: 'storeLocationCode',
        key: 'storeLocationCode'
      },
      {
        title: '上架至',
        width: 150,
        dataIndex: 'shelfStoreLocationCode',
        key: 'shelfStoreLocationCode',
        render: (text, record, index) => {
          //  此次只更改P01 默认仓位按照原逻辑走，只允许修改上架到存储位开头为BZ的
          let canChangeFlag = this.props.modalData
            .canUpdateShelfStoreLocationCode
          const {userLoation} = this.props
          if (!canChangeFlag) {
            return text
          }
          return (
            <CsxFormSearch
              dateApi={API_STORAGE_SEARCH_GETPRODUCTKEYVALUES}
              searchParams={{limitNum: 100}}
              searchParamsKey="keyword"
              disabledReg={(optionData) => {
                if (
                  String(optionData.storageLocationCode).trim() ===
                  String(record.storeLocationCode).trim()
                ) {
                  return true
                }
                return false
              }}
              selectItem={{
                data: {
                  value: record.shelfStoreLocationCode,
                  children: record.shelfStoreLocationCode
                },
                key: 'storageLocationCode',
                valueKey: ['storageLocationCode']
              }}
              onChangeSelect={(item) => {
                record.shelfStoreLocationCode = item.value
                this.forceUpdate()
              }}
              onSelect={(item) => {
                const {storeLocationCode = ''} = record
                // if (
                //   String(storeLocationCode).trim() === String(item.value).trim()
                // ) {
                //   record.shelfStoreLocationCode = ''
                //   this.forceUpdate()
                //   return message.error(
                //     `收货至[${storeLocationCode}]和上架至[${item.value}]不可以相同`
                //   )
                // }
                record.shelfStoreLocationCode = item.value
                this.forceUpdate()
              }}
            />
          )
        }
      },
      {
        title: '当前储/捡位数量',
        width: 150,
        dataIndex: 'shelfStorageLocationQty',
        key: 'shelfStorageLocationQty'
      },
      {
        title: '备注',
        width: 150,
        dataIndex: 'remark',
        key: 'remark',
        render: (text, record, index) => (
          <Input
            value={record.remark}
            style={{width: '100px'}}
            onChange={(e) => {
              record.remark = e.target.value
              this.forceUpdate()
            }}
          />
        )
      }
    ]
    this.columns2 = [
      {
        title: '商品编码',
        width: 150,
        dataIndex: 'productCode',
        key: 'productCode'
      },
      {
        title: '商品条码',
        width: 150,
        dataIndex: 'productBarCode',
        key: 'productBarCode'
      },
      {
        title: '商品名称',
        width: 200,
        dataIndex: 'productName',
        key: 'productName'
      },
      {
        title: '收货',
        width: 150,
        dataIndex: 'expectReceiveQty',
        key: 'expectReceiveQty',
        render: (text, record, index) => (
          <CsxValidateInput
            id={`inWarehouseList${index + 1}`}
            onFocus={(e) => {
              this.elOnFocus(e)
            }}
            defaultValue={
              this.props.modalData.entryType == 'T06' && record.shippedQty
                ? (record.unit && record.unit.toUpperCase()) == 'KG'
                  ? Number(
                      (record.shippedQty - record.receiveQty).toFixed(3)
                    ) || ''
                  : Number(record.shippedQty - record.receiveQty) || ''
                : ''
            }
            style={{width: '100px'}}
            reg={
              (record.unit && record.unit.toUpperCase()) == 'KG'
                ? 'TWOO'
                : 'REGO'
            }
            error={record.receiveQtyInput && record.receiveQtyInput.error}
            handleOnchange={(data) => {
              if (data.value) {
                record.receiveQtyInput = data
              } else {
                record.receiveQtyInput = ''
              }
              record.noDefault = true
              this.forceUpdate()
            }}
          />
        )
      },
      {
        title: '收货单位',
        width: 100,
        dataIndex: 'unit',
        key: 'unit'
      },
      {
        title: '生产日期',
        width: 150,
        dataIndex: 'produceDate',
        key: 'produceDate',
        render: (text, record, index) => (
          <DatePicker
            placeholder={moment(new Date()).format('YYYY-MM-DD')}
            onChange={(moment, string) => {
              record.produceDate = string
            }}
            format={'YYYY-MM-DD'}
            disabledDate={(current) =>
              current && current.valueOf() > Date.now()
            }
          />
        )
      },
      {
        title: '发货数',
        width: 100,
        dataIndex: 'shippedQty',
        key: 'shippedQty'
      },
      {
        title: '已收数',
        width: 100,
        dataIndex: 'receiveQty',
        key: 'receiveQty'
      },
      {
        title: '收货至',
        width: 100,
        dataIndex: 'storeLocationCode',
        key: 'storeLocationCode'
      },
      {
        title: '上架至',
        width: 100,
        dataIndex: 'shelfStoreLocationCode',
        key: 'shelfStoreLocationCode'
      },
      {
        title: '当前储/捡位数量',
        width: 150,
        dataIndex: 'shelfStorageLocationQty',
        key: 'shelfStorageLocationQty'
      },
      {
        title: '备注',
        width: 150,
        dataIndex: 'remark',
        key: 'remark',
        render: (text, record, index) => (
          <Input
            value={record.remark}
            style={{width: '100px'}}
            onChange={(e) => {
              record.remark = e.target.value
              this.forceUpdate()
            }}
          />
        )
      }
    ]
  }
  componentDidMount() {
    this._getAsyncData()
    document.addEventListener('keydown', this.handleKeyDown)
  }
  componentWillUnmount() {
    document.removeEventListener('keydown', this.handleKeyDown)
  }
  elOnFocus = (e) => {
    e = e.nativeEvent
    this.foucsIndex = Number(e.target.id.replace(this.foucsName, ''))
  }
  // 点击上下按键
  handleKeyDown = (e) => {
    e = e || window.event
    switch (e.keyCode) {
      case 38:
        this.keyDownUp()
        break //上
      case 40:
        this.keyDownDown()
        break //下
      default:
        break
    }
  }
  keyDownUp = () => {
    this.foucsIndex -= 1
    let el = document.getElementById(this.foucsName + this.foucsIndex)
    // 判断按键后的元素是否存在
    if (el) {
      el.focus()
    } else {
      this.foucsIndex += 1
    }
  }
  keyDownDown = () => {
    this.foucsIndex += 1
    let el = document.getElementById(this.foucsName + this.foucsIndex)
    if (el) {
      el.focus()
    } else {
      this.foucsIndex -= 1
    }
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData() {
    const {handleGetDetail, modalId} = this.props
    handleGetDetail(
      {searchParams: {operationType: 'R'}, urlParams: modalId},
      () => {
        // this.setState({
        //   canChangeFlag: 4444
        // })
        // this.handleCloseModal()
      }
    )
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({isShowDeliveryModal: false})
  }
  // 收货
  delivery = () => {
    const {handleDelivery, modalData} = this.props
    let reqData = []
    let error = false //收货数量的格式判断
    let haveRece = false //是否有收货
    let haveShelfStoreLocationCode = true //是否有上架位置
    let sameZone = {
      falg: false, //是否收货至和上架至位置相同
      storeLocationCode: '', //收货至位置
      shelfStoreLocationCode: '' //上架至位置
    }
    modalData.productItems
      .filter(
        (o) =>
          o.receiveQtyInput ||
          (!o.noDefault &&
            modalData.entryType == 'T06' &&
            o.shippedQty &&
            o.shippedQty - o.receiveQty)
      )
      .map((o, i) => {
        if (o.receiveQtyInput && o.receiveQtyInput.error) {
          error = true
          return
        }
        if ((o.receiveQtyInput && o.receiveQtyInput.value) || o.shippedQty) {
          haveRece = true
        }
        if (!o.shelfStoreLocationCode) {
          haveShelfStoreLocationCode = false
        }
        if (
          String(o.storeLocationCode).trim() ===
          String(o.shelfStoreLocationCode).trim()
        ) {
          sameZone = {
            falg: true, //是否收货至和上架至位置相同
            storeLocationCode: o.storeLocationCode, //上架至
            shelfStoreLocationCode: o.shelfStoreLocationCode //收货至
          }
        }
        reqData.push({
          id: o.id,
          orderCode: o.orderCode,
          receiveQty: o.receiveQtyInput
            ? o.receiveQtyInput.value
            : modalData.entryType == 'T06' && o.shippedQty
            ? (o.unit && o.unit.toUpperCase()) == 'KG'
              ? Number((o.shippedQty - o.receiveQty).toFixed(3)) || ''
              : Number(o.shippedQty - o.receiveQty) || ''
            : '',
          shelfStoreLocationCode: o.shelfStoreLocationCode,
          produceDate: o.produceDate || moment(new Date()).format('YYYY-MM-DD'),
          remark: o.remark
        })
      })
    if (error) {
      message.error('请输入正确的数量')
      return
    }
    if (!haveRece) {
      message.error('请输入要收货的数量')
      return
    }
    if (!haveShelfStoreLocationCode) {
      message.error('请输入上架位')
      return
    }
    if (sameZone.falg) {
      message.error(
        `收货至[${sameZone.storeLocationCode}]和上架至[${sameZone.shelfStoreLocationCode}]不可以相同`
      )
      return
    }

    handleDelivery({data: reqData, urlParams: [modalData.id]}, () => {
      message.success('操作成功')
      const {handleGetList, searchParams, page, size, userLoation} = this.props
      handleGetList({searchParams: {page, size, ...searchParams}})
      this.handleCloseModal()
    })
  }
  render() {
    const {deliveryTableLoading, modalData} = this.props
    return (
      <Modal
        title="收货上架操作"
        visible={true}
        onCancel={this.handleCloseModal}
        footer={null}
        mask={false}
        width={1000}
      >
        <div className="content">
          <div className="header">
            <Row type="flex" justify="start">
              <Col span={8} className="delBox">
                <label>入库单：</label>
                <span>{modalData.orderCode || ''}</span>
              </Col>
              <Col span={8} className="delBox">
                <label>业务类型：</label>
                <span>{modalData.businessType || ''}</span>
              </Col>
              <Col span={8} className="delBox">
                <label>货主：</label>
                <span>{modalData.supplierName || ''}</span>
              </Col>
              <Col span={8} className="delBox">
                <label>供应商/门店：</label>
                <span>{`${modalData.supplierCode || ''}-${
                  modalData.supplierName || ''
                }`}</span>
              </Col>
              <Col span={8} className="delBox">
                <label>收货地点：</label>
                <span>{`${modalData.receiveLocationCode || ''}-${
                  modalData.receiveLocationName || ''
                }`}</span>
              </Col>
              <Col span={8} className="delBox">
                <label>收货库区：</label>
                <span>{modalData.receiveReservoirAreaCode || ''}</span>
              </Col>
            </Row>
            <Row
              type="flex"
              justify="center"
              gutter={16}
              style={{marginTop: '28px'}}
            >
              <Col className="delBox">
                <label>商品数量（已收/总量）：</label>
                <span>
                  {modalData.receiveProductCount} /{modalData.productCount}
                </span>
              </Col>
              <Col className="delBox">
                <label>商品种类（已收/总量）：</label>
                <span>
                  {modalData.receiveProductCategoryCount} /
                  {modalData.productCategoryCount}
                </span>
              </Col>
            </Row>
          </div>
          <div className="foot">
            <div className="table-group" style={{margin: 0}}>
              <Table
                rowKey={(record) => record.id}
                loading={deliveryTableLoading}
                columns={
                  modalData.entryType == 'P02' ? this.columns2 : this.columns
                }
                scroll={{x: 'max-content', y: 400}}
                dataSource={modalData.productItems}
                pagination={false}
              />
              <Row type="flex" justify="start" style={{marginTop: '15px'}}>
                <Col>
                  <p style={{color: 'red'}}>
                    注: 称重单位（kg）可输入三位小数，非称重单位只能输入整数
                  </p>
                </Col>
              </Row>
              <Row type="flex" justify="end" style={{marginTop: '15px'}}>
                <Col>
                {
                   (!modalData.productItems) || (Array.isArray(modalData.productItems) && modalData.productItems.length == 0)

                   ? null
                    : <Button
                       // 当收货上架的列表为空的时候，确定按钮不展示
                     disabled={modalData.receiveStatus == 2 }
                     type="primary"
                     onClick={this.delivery}
                   >
                     确认
                   </Button>
                }

                </Col>
              </Row>
            </div>
          </div>
        </div>
      </Modal>
    )
  }
}



