import React, {PureComponent} from 'react'
import {
  message,
  Button,
  Modal,
  Tabs
} from 'antd'
import style from './style/Tool.scss'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {setVariant} from 'Variant'
import {getNowPageId} from '../../global/components/menuData'
const confirm = Modal.confirm
const {TabPane} = Tabs

@connect(
  state => ({...state.inWarehouseList}),
  mapDispatchToProps
)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {
    }
  }
  componentDidMount() {
    const {GetButtonPower} = this.props
    const MEUN = JSON.parse(localStorage.getItem('b2bUserMenu'))
    const ID = getNowPageId(MEUN, window.location.pathname)
    GetButtonPower({urlParams: [ID]})
  }
  close = () => {
    const {selectedRowKeys, handleClose, handleGetList, searchParams, page, size} = this.props
    if (!selectedRowKeys.length) {
      message.error('请勾选订单')
      return
    }
    confirm({
      title: '提示',
      content: '关单后，商品将无法再进行收货上架操作，确认完成关单？',
      okText: '确认',
      width: 520,
      cancelText: '返回',
      onOk() {
        handleClose({data: {orderIds: selectedRowKeys.toString()}}, () => {
          message.success('操作成功')
          handleGetList({searchParams: {page, size, ...searchParams}})
        })
      },
      onCancel() {
        console.log('Cancel')
      }
    })
  }

  /* 打印预览 */
  handleBrowse = (record) => {
    const {selectedRowKeys} = this.props
    if (!selectedRowKeys.length) {
      message.error('请勾选订单')
      return
    }
    confirm({
      title: '提示',
      content: '请选择打印类型',
      okText: '按批次打印',
      // cancelText: '全量打印',
      width: 520,
      onOk() {
        setVariant('wms/inWarehouseList', selectedRowKeys)
        window.open('/wms/inWarehouseList/Print/2', 'newWindow', 'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no')
      }
      // onCancel() {
      //   setVariant('wms/inWarehouseList', selectedRowKeys)
      //   window.open('/wms/inWarehouseList/Print/1', 'newWindow', 'height=500, width=1300, top=0, left=0, toolbar=no, menubar=no, scrollbars=no, resizable=no, location=no, status=no')
      // }
    })
  }
  // 导出
  handleExport = () => {
    const {handleExport, searchParams, page, size, tableActiveKey, selectedRows} = this.props
    let reqObj = []
    selectedRows.map(n => reqObj.push(n.orderCode)).join(',')
    console.log(reqObj)
    handleExport({searchParams: {page, size, receiveStatus: tableActiveKey, ...searchParams, orderCodes: reqObj.length == 0 ? '' : reqObj, productName: searchParams.productName ? searchParams.productName.children : ''}}, () => {
      message.success('导出成功')
    })
  }
  // 查询数据
  query = (tableActiveKey) => {
    const {handleGetList, searchParams, size, update} = this.props
    let reqObj = {...searchParams}
    update({tableActiveKey})
    handleGetList({searchParams: {...reqObj, page: 1, receiveStatus: tableActiveKey, size}}, true)
  }
  render() {
    const {tableActiveKey, tab0, tab1, tab2, tab3, inWarehouseListCloseOrder, inWarehouseListPrintOrder, inWarehouseListExport} = this.props
    return (
      <div className={style['table-tool']} style={{paddingBottom: 0}}>
        {
          (tableActiveKey == '0' || tableActiveKey == '1') && inWarehouseListCloseOrder && <Button type="primary" className={style.btn} onClick={this.close}>关单</Button>
        }
        {
          inWarehouseListPrintOrder && <Button type="primary" className={style.btn} onClick={this.handleBrowse}>打印收货单</Button>
        }
        {
          inWarehouseListExport && <Button type="primary" className={style.btn} onClick={this.handleExport}>导出</Button>
        }
        <Tabs onChange={this.query} activeKey={tableActiveKey} >
          <TabPane tab={`待收货(${tab0 || 0})`} key="0"></TabPane>
          <TabPane tab={`收货中(${tab1 || 0})`} key="1"></TabPane>
          <TabPane tab={`已关单(${tab2 || 0})`} key="2"></TabPane>
          <TabPane tab={`已取消(${tab3 || 0})`} key="3"></TabPane>
        </Tabs>
      </div>
    )
  }
}



