import React, {PureComponent} from 'react'
import {
  Form,
  Button,
  Row,
  Col,
  DatePicker,
  Input,
  message,
  Checkbox
} from 'antd'
import style from './style/Form.scss'
import {
  API_ENTRYORDERS_KEYVALUES,
  API_WDENTRYORDERS_SUPPLIERS,
  API_PRODUCT_GETPRODUCTKEYVALUES,
  API_RETURN_REASON_LIST_PAGE
} from 'api'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {CsxSelect, CsxSearch, CsxMultipleSelect} from 'Form'
import {PROCUREMENT} from 'Mock'
const {RangePicker} = DatePicker
import moment from 'moment'

@connect(
  (state) => ({...state.inWarehouseList, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
  }
  componentWillMount() {
    this.initDateOpt()
  }
  // 设置默认的时间  前一月 - 当天
  initDateOpt(values) {
    const {update} = this.props
    const preMonth = moment().subtract(1, 'M').startOf('day')
    update({searchParams: {initDate2: [preMonth, moment().endOf('day')]}})
  }

  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }
  // 查询数据
  query = () => {
    const {handleGetList, searchParams, size} = this.props
    let reqObj = {...searchParams}
    const {initDate2 = []} = searchParams
    // 判断创建时时间是否为空
    if (!initDate2 || (Array.isArray(initDate2) && initDate2.length < 2)) {
      message.error('请选择创建时间后搜索')
    }

    handleGetList({searchParams: {...reqObj, page: 1, size}})
  }
  // 重置
  reset = () => {
    const {update} = this.props
    update({searchParams: {}})
    // 重置时间
    this.initDateOpt()
  }
  render() {
    const {
      searchParams: {
        productName,
        initDate,
        initDate1,
        initDate2,
        supplierName,
        sendLocationCode,
        businessType,
        entryType,
        allReceiveFlag,
        // receiveStatus,
        // allShelfFlag,
        orderCode,
        directFlag,
        shelfStatus,
        tranferFlag,
        priceFixFlag,
        productCode,
        returnReasonCode,
        orderSource,
        giftFlag,
        linkOrderCode
      },
      userLoation
    } = this.props
    return (
      <Form className={style.form}>
        <Row type="flex" justify="start">
          <Col span={6}>
            <div className="commonInput">
              <label>订单查询：</label>
              <div className="box">
                <Input
                  placeholder="支持各种单号查询"
                  value={orderCode}
                  onChange={(e) => {
                    this.onInputChange({orderCode: e.target.value})
                  }}
                />
              </div>
            </div>
          </Col>
          <Col span={6}>
            <CsxSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label="商品编码"
              searchParamsKey="productCode"
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{
                data: productCode,
                key: 'key',
                valueKey: ['key', 'value']
              }}
              onSelect={(value) => {
                this.onInputChange({productCode: value})
              }}
              onChangeSelect={(value) => {
                this.onInputChange({productCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSearch
              dateApi={API_PRODUCT_GETPRODUCTKEYVALUES}
              label="商品名称"
              searchParamsKey="productName"
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{
                data: productName,
                key: 'key',
                valueKey: ['key', 'value']
              }}
              onChangeSelect={(value) => {
                this.onInputChange({productName: value})
              }}
              onSelect={(value) => {
                this.onInputChange({productName: value})
              }}
            />
          </Col>
          {/* <Col span={6}>
            <CsxSelect
              dateApi={API_ENTRYORDERS_KEYVALUES}
              label='收货状态'
              searchParams={{type: 'S'}}
              selectItem={{data: receiveStatus, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({receiveStatus: value})
              }}
            />
          </Col> */}
          <Col span={6}>
            <CsxSelect
              dateApi={API_ENTRYORDERS_KEYVALUES}
              label="入库类型"
              searchParams={{type: 'E'}}
              selectItem={{data: entryType, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({entryType: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_ENTRYORDERS_KEYVALUES}
              label="业务类型"
              searchParams={{type: 'B'}}
              selectItem={{data: businessType, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({businessType: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxMultipleSelect
              dateApi={API_RETURN_REASON_LIST_PAGE}
              label="退货原因"
              selectItem={{
                data: returnReasonCode,
                key: 'reasonCode',
                valueKey: ['reasonCode', 'reasonRemark']
              }}
              onChangeSelect={(value) => {
                this.onInputChange({returnReasonCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <div className="commonInput">
              <label>来源地点：</label>
              <div className="box">
                <Input
                  placeholder="批量查询编码用,号隔开"
                  value={sendLocationCode}
                  onChange={(e) => {
                    this.onInputChange({sendLocationCode: e.target.value})
                  }}
                />
              </div>
            </div>
          </Col>
          <Col span={6}>
            <CsxSearch
              dateApi={API_WDENTRYORDERS_SUPPLIERS}
              label="供应商"
              searchParams={{warehouseCode: userLoation.value}}
              searchParamsKey="supplierCode"
              selectItem={{
                data: supplierName,
                key: 'key',
                valueKey: ['key', 'value']
              }}
              onChangeSelect={(value) => {
                this.onInputChange({supplierName: value})
              }}
              onSelect={(value) => {
                this.onInputChange({supplierName: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSelect
              dateApi={API_ENTRYORDERS_KEYVALUES}
              label="上架状态"
              searchParams={{type: 'U'}}
              selectItem={{data: shelfStatus, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({shelfStatus: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxMultipleSelect
              data={PROCUREMENT}
              label="采购用途"
              selectItem={{data: orderSource, key: 'key', valueKey: 'value'}}
              onChangeSelect={(value) => {
                this.onInputChange({orderSource: value})
              }}
            />
          </Col>
          <Col span={6}>
            <div className="commonInput">
              <label>关联单号：</label>
              <div className="box">
                <Input
                  value={linkOrderCode}
                  onChange={(e) => {
                    this.onInputChange({linkOrderCode: e.target.value})
                  }}
                />
              </div>
            </div>
          </Col>
          <Col span={6}>
            <div className="commonInput">
              <label>预计收货时间：</label>
              <div className="box" style={{marginRight: 0}}>
                <RangePicker
                  onChange={(moment, string) => {
                    this.onInputChange({initDate: moment})
                  }}
                  format={'YYYY-MM-DD'}
                  value={initDate}
                />
              </div>
            </div>
          </Col>
          <Col span={6}>
            <div className="commonInput">
              <label>关单时间：</label>
              <div className="box" style={{marginRight: 0}}>
                <RangePicker
                  onChange={(moment, string) => {
                    this.onInputChange({initDate1: moment})
                  }}
                  format={'YYYY-MM-DD'}
                  value={initDate1}
                />
              </div>
            </div>
          </Col>
          <Col span={6}>
            <div className="commonInput">
              <label>创建时间：</label>
              <div className="box" style={{marginRight: 0}}>
                <RangePicker
                  onChange={(moment, string) => {
                    this.onInputChange({initDate2: moment})
                  }}
                  format={'YYYY-MM-DD'}
                  value={initDate2}
                  allowClear={false}
                />
              </div>
            </div>
          </Col>
          <Col span={12}>
            <Checkbox
              style={{marginLeft: 30}}
              checked={allReceiveFlag ? true : false}
              onChange={(e) => {
                this.onInputChange({allReceiveFlag: e.target.checked})
              }}
            >
              未全量收货
            </Checkbox>
            {/* <Checkbox checked={allShelfFlag ? true : false} onChange={(e) => {
              this.onInputChange({allShelfFlag: e.target.checked})
            }}>未全量上架</Checkbox> */}
            <Checkbox
              checked={directFlag ? true : false}
              onChange={(e) => {
                this.onInputChange({directFlag: e.target.checked})
              }}
            >
              账面直通
            </Checkbox>
            <Checkbox
              checked={tranferFlag ? true : false}
              onChange={(e) => {
                this.onInputChange({tranferFlag: e.target.checked})
              }}
            >
              调拨已出库
            </Checkbox>
            <Checkbox
              checked={giftFlag ? true : false}
              onChange={(e) => {
                this.onInputChange({giftFlag: e.target.checked})
              }}
            >
              赠品入库
            </Checkbox>
            <Checkbox
              checked={priceFixFlag ? true : false}
              onChange={(e) => {
                this.onInputChange({priceFixFlag: e.target.checked})
              }}
            >
              价格补救
            </Checkbox>
          </Col>
          <Col span={24}>
            <Row type="flex" justify="end">
              <Button
                type="primary"
                className={style.mr10}
                onClick={this.query}
              >
                查询
              </Button>
              <Button
                type="primary"
                className={style.mr10}
                onClick={this.reset}
              >
                重置
              </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(App)



