import React, {PureComponent} from 'react'
import {Table, Modal, Icon, Popover} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import JsBarcode from 'jsbarcode'
const confirm = Modal.confirm

@connect(
  (state) => ({...state.inWarehouseList, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {}
    this.columns = [
      {
        title: '操作',
        width: 200,
        dataIndex: 'defaultStorageLocationFlag',
        key: 'defaultStorageLocationFlag',
        render: (text, record, index) => (
          <div className="tableOperation">
            {record.receiveStatus == 2 || record.receiveStatus == 3 ? null : (
              <span
                onClick={() => {
                  this.onEdit(
                    'isShowDeliveryModal',
                    record.id,
                    record.autoStatus == 1
                  )
                }}
              >
                一键收货上架
              </span>
            )}
            <span
              onClick={() => {
                this.onEdit('isShowRecordModal', record.orderCode)
              }}
            >
              记录
            </span>
          </div>
        )
      },
      {
        title: '入库单',
        width: 150,
        dataIndex: 'orderCode',
        key: 'orderCode',
        render: (text, record, index) => (
          <React.Fragment>
            <a
              onClick={() => {
                const {
                  history: {location}
                } = this.props
                window.open(`${location.pathname}/${text}`)
                // history.push(`${location.pathname}/${record.orderCode}`)
              }}
            >
              {text}
            </a>
            <Popover
              content={
                <img
                  id={`key${text}`}
                  style={{width: '221px', height: '50px'}}
                />
              }
              title={text}
              trigger="click"
            >
              <Icon
                type="file-text"
                className="my-icon"
                onClick={this.handlePrint.bind(this, text)}
              />
            </Popover>
          </React.Fragment>
        )
      },
      {
        title: '来源单号',
        width: 180,
        dataIndex: 'originOrderCode',
        key: 'originOrderCode',
        render: (text, record, index) => (
          <React.Fragment>
            <span>{text}</span>
            <Popover
              content={
                <img
                  id={`key${text}`}
                  style={{width: '287px', height: '50px'}}
                />
              }
              title={text}
              trigger="click"
            >
              <Icon
                type="file-text"
                className="my-icon"
                onClick={this.handlePrint.bind(this, text)}
              />
            </Popover>
          </React.Fragment>
        )
      },
      {
        title: '关联单号',
        width: 180,
        dataIndex: 'linkOrderCode',
        key: 'linkOrderCode',
        render: (text, record, index) =>
          text ? (
            <React.Fragment>
              <span>{text}</span>
              <Popover
                content={
                  <img
                    id={`key${text}`}
                    style={{width: '287px', height: '50px'}}
                  />
                }
                title={text}
                trigger="click"
              >
                <Icon
                  type="file-text"
                  className="my-icon"
                  onClick={this.handlePrint.bind(this, text)}
                />
              </Popover>
            </React.Fragment>
          ) : (
            ''
          )
      },
      {
        title: '收货状态',
        width: 100,
        dataIndex: 'receiveStatusDesc',
        key: 'receiveStatusDesc'
      },
      {
        title: '入库类型',
        width: 100,
        dataIndex: 'entryType',
        key: 'entryType'
      },
      {
        title: '业务类型',
        width: 100,
        dataIndex: 'businessType',
        key: 'businessType'
      },
      {
        title: '采购用途',
        width: 100,
        dataIndex: 'orderSourceDesc',
        key: 'orderSourceDesc'
      },
      {
        title: '是否全量收货',
        width: 100,
        dataIndex: 'allReceiveFlag',
        key: 'allReceiveFlag'
      },
      {
        title: '入库单打印次数',
        width: 150,
        dataIndex: 'printTimes',
        key: 'printTimes'
      },
      {
        title: '备注',
        width: 150,
        dataIndex: 'remark',
        key: 'remark'
      },
      {
        title: '创建人',
        width: 100,
        dataIndex: 'createBy',
        key: 'createBy'
      },
      {
        title: '创建时间',
        width: 150,
        dataIndex: 'createTime',
        key: 'createTime'
      },
      // {
      //   title: '最晚到货日期',
      //   width: 150,
      //   dataIndex: 'planReceiveDate',
      //   key: 'planReceiveDate'
      // },
      // {
      //   title: '预计发货日期',
      //   width: 150,
      //   dataIndex: 'planReceiveDate',
      //   key: 'planReceiveDate',
      //   render: () => 33333
      // },
      {
        title: '预计收货时间',
        width: 150,
        dataIndex: 'planReceiveDate',
        key: 'planReceiveDate'
      },
      {
        title: '关单时间',
        width: 150,
        dataIndex: 'closeTime',
        key: 'closeTime'
      },
      {
        title: '关单人',
        width: 100,
        dataIndex: 'closeBy',
        key: 'closeBy'
      },
      {
        title: '关联操作订单',
        width: 150,
        dataIndex: 'linkOperateOrderCode',
        key: 'linkOperateOrderCode'
      },
      {
        title: '外部单号',
        width: 150,
        dataIndex: 'outsideOrderCode',
        key: 'outsideOrderCode'
      },
      {
        title: '自动执行',
        width: 100,
        dataIndex: 'autoStatusDesc',
        key: 'autoStatusDesc'
      }
    ]
  }
  componentDidMount() {
    const {handleGetList, searchParams, page, size, userLoation} = this.props
    if (!userLoation || !userLoation.value) return
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, userLoation} = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({
        searchParams: {page: 1, size: newProps.size, ...newProps.searchParams}
      })
    }
  }

  // 打印条形码
  handlePrint = (text) => {
    setTimeout(() => {
      JsBarcode(`#key${text}`, text, {
        width: 1.5,
        height: 30,
        displayValue: false
      })
    })
  }
  //一些操作
  onEdit = (modalType, id, flag) => {
    console.log(modalType, id, flag)
    const {update} = this.props
    if (flag) {
      confirm({
        title: '温馨提示',
        content: '此订单将自动入库，无需手动操作',
        okText: '确认',
        width: 520,
        cancelText: '返回',
        onOk() {
          console.log('Ok')
        },
        onCancel() {
          console.log('Cancel')
        }
      })
    } else {
      update({[modalType]: true, modalId: [id], modalData: {}})
    }
  }

  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size, tableActiveKey} = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList(
          {
            searchParams: {
              page,
              size,
              receiveStatus: tableActiveKey,
              ...searchParams,
              isAsc: true,
              columns: sorter.field
            }
          },
          true
        )
      } else {
        handleGetList(
          {
            searchParams: {
              page,
              size,
              receiveStatus: tableActiveKey,
              ...searchParams,
              isAsc: false,
              columns: sorter.field
            }
          },
          true
        )
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList(
        {
          searchParams: {
            page: pagination.current,
            size: pagination.pageSize,
            receiveStatus: tableActiveKey,
            ...searchParams
          }
        },
        true
      )
    }
  }
  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult,
      selectedRowKeys
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    let rowSelection = {
      onChange: (selectedRowKeys, selectedRows) => {
        const {update} = this.props
        update({selectedRowKeys, selectedRows})
      },
      selectedRowKeys,
      getCheckboxProps: (record) => ({
        // disabled: record.receiveStatus == '2' // Column configuration not to be checked
      })
    }
    return (
      <div className="table-group">
        <Table
          rowKey={(record) => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          onChange={this.onChange}
          pagination={pagination}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}



